            addDays: "3个月"
          },
          {
            name: '半年付',
            cycleType: 'semiannually',
            addDays: "6个月"
          },
          {
            name: '年付',
            cycleType: 'annually',
            addDays: "12个月"
          }
        ],
        serviceDetail: {
          id: '',
          name: '',
          expire: '',
          reset: '',
          billingCycle: '',
          residue: 0,
          total: 0,
          addDays: "1个月",
          subscribeUrl: ''
        },
        clientList: [
          'jetstream',
          'v2rayu',
          'v2rayng',
          'quantumult',
          'shadowrocket',
          'clash',
          'surge',
          'switchyomega',
          'lede',
          'merlin',
          'gost',
          'v2rayn'
        ],
        lastInvoiceId: 0
      };
    },
    mounted() {
      this.getServicesDetail()
      this.getArticleData();
      this.getInvoices()
      // this.tutorialsHeight = window.screen.height - 400
      // console.log(this.tutorialsHeight)
      // console.log(window.location)
    },
    methods: {
      async getArticleData() {
        let that = this;
        await this.$request.getZendeskArticles(function (data) {
          if (data.status === 200) {
            const result = JSON.parse(JSON.stringify(data.data));
            that.articles = result.articles;
            that.$request.getZendeskCategories(function (data) {
              if (data.status === 200) {
                const result = JSON.parse(JSON.stringify(data.data));
                that.categories = result.categories.filter(categoriesItem => {
                  return categoriesItem.id === 360002460371
                });
                // console.log(that.categories)
                that.$request.getZendeskSections(function (data) {
                  if (data.status === 200) {
                    const result = JSON.parse(JSON.stringify(data.data));
                    that.sections = result.sections;
                    // disposal data
                    that.categories.forEach(categoryItem => {
                      categoryItem['sections'] = [];
                      // if (categoryItem.id === 360002460371) {
                      that.sections.forEach(sectionItem => {
                        if (sectionItem['category_id'] === categoryItem['id']) {
                          sectionItem['articles'] = [];
                          that.articles.forEach(article => {
                            if (article['section_id'] === sectionItem['id'] &&
                                !!article.label_names && article.label_names.length > 0) {
                              article['isShow'] = false;
                              sectionItem['articles'].push(article);
                              //that.targetSectionMB.push(article)
                              that.articlesBuf.push(article)
                            }
                          });
                          sectionItem['isShow'] = false
                          categoryItem['sections'].push(sectionItem);
                          that.targetSection.push(sectionItem)
                        }
                      });
                      // }

                    });
                    that.catalogue = that.categories;
                    that.targetSection[0].isShow = true
                    that.targetSection[0]['articles'][0].isShow = true
                    that.selectedArticle = that.targetSection[0]['articles'][0]
                    that.getSubscribeUrl()
                    // console.log(that.targetSection)
                    // console.log("done with text")
                  }
                    that.isArticleLoading = false
                });
              }
            });
          }
        });
      },
      async getServicesDetail() {
        const that = this
        await this.$request.getAllServices(function (data) {
          if (data == null) {
             that.isNetWorkError = true
          } else {
            if (data.status === 200) {
              // console.log(`services list: `)
              // console.log(data.data)
              // Handle service's name and billingcycle
              //that.serviceList = data.data.products
              that.serviceList = data.data.products
              //     .filter(data => {
              //   return data.subscribeUrl.startsWith('https://api.savoy.click/api')
              // });
              that.serviceList.forEach(function (data) {
                data.name = data.name.toUpperCase().replace('UNLIMITED', '無限量')
                if (data.billingcycle == null ) {
                  data['addDays'] = '1个月';
                } else {
                  that.selectBillingCycleList.forEach(function (cycleData) {
                    if (cycleData.cycleType === data.billingcycle) {
                      data.billingcycle = cycleData.name;
                      data['addDays'] = cycleData.addDays;
                    }
                  })
                }
              })
              that.selectedService(0)
              // console.log(that.serviceList)
            } else {
              that.isNetWorkError = true
            }
          }
        })
      },
      async selectedService(index) {

        if (!!this.serviceList && !!this.serviceList[index]) {
          this.isServiceLoading = true
          const that = this
          await this.$request.getServiceDetail(that.serviceList[index].id, function (data) {
            if (data.status === 200) {
              // console.log(`service detail:`)
              // console.log(data.data)
              that.serviceDetail.id = that.serviceList[index].id
              that.serviceDetail.name = that.serviceList[index].name
              that.serviceDetail.expire = that.daysBetween(new Date(), new Date(that.serviceList[index].nextduedate))
              that.serviceDetail.reset = that.countResetDay(that.serviceList[index].regdate)
              that.serviceDetail.billingCycle = that.serviceList[index].billingcycle;
              that.serviceDetail.addDays = that.serviceList[index].addDays;

              that.serviceDetail.total = Math.floor((parseInt(data.data.usage.allowed) / 1024 / 1024 / 1024) * 100) / 100
              that.serviceDetail.residue = Math.floor(((parseInt(data.data.usage.allowed) - parseInt(data.data.usage.upload) - parseInt(data.data.usage.download)) / 1024 / 1024 / 1024) * 100) / 100
              that.serviceDetail.subscribeUrl = that.serviceList[index].subscribeUrl
              that.percent = parseInt(that.serviceDetail.residue * 100 / that.serviceDetail.total)
              // console.log(`serviceDetail:`)
              // console.log(that.serviceDetail)
              // console.log("done with services")
              that.getSubscribeUrl()
              that.isServiceLoading = false
            }
          })

        } else {
          this.isServiceLoading = false
        }
      },
      async getInvoices() {
        let params = {
          limitnum: 10,
          limitstart: 0,
          orderby: 'id',
          order: 'descend',
          status: 'Unpaid',
        }
        const that = this
        await this.$request.getUsersInvoices(params, function (data) {
          if (data.status === 200 && !!data.data.invoices.invoice && data.data.invoices.invoice.length > 0) {
            that.lastInvoiceId = data.data.invoices.invoice[0].id
          }
        });
      },
      daysBetween(sDate1, sDate2) {
        //Date.parse() 解析一个日期时间字符串，并返回1970/1/1 午夜距离该日期时间的毫秒数
        let time1 = Date.parse(sDate1);
        let time2 = Date.parse(sDate2);
        let nDays = Math.abs(parseInt((time2 - time1) / 1000 / 3600 / 24));
        return nDays;
      },
      countResetDay(regDate) {
        let date = new Date()
        let regDay = parseInt(parseInt(regDate.split('-')[2]))
        let currentDay = date.getDate()
        let lastDay = new Date(date.getFullYear(), date.getMonth() + 1, '0').getDate()
        let nextLastDay = new Date(date.getFullYear(), date.getMonth() + 2, '0').getDate()
        if (regDay > currentDay) {//当月还没到重置日期
          if (lastDay === currentDay) {
            return nextLastDay.toString()
          }
          return (Math.min(regDay, lastDay) - currentDay).toString()
        } else {
          return (lastDay - currentDay + Math.min(regDay, nextLastDay)).toString()
        }
      },
      countAddDay(billingcycle) {
        if (billingcycle == "") {

        }
      },
      getSubscribeUrl() {
        if (this.serviceDetail.subscribeUrl.length > 0 && !!this.selectedArticle['label_names'] && this.selectedArticle['label_names'].length > 0) {
          let labelName = this.selectedArticle['label_names'][0].toLowerCase()
          const that = this
          this.clientList.forEach(function (data) {
            if (labelName.indexOf(data) > -1) {
              // console.log(that.serviceDetail)
              if (data === 'lede' || data === 'merlin' || data === 'v2rayn') {
                that.subscribeUrl = `${that.serviceDetail.subscribeUrl}?client=v2rayu`
              } else {
                that.subscribeUrl = `${that.serviceDetail.subscribeUrl}?client=${data}`
              }
            }
          })
        }
      },
      /*switchArticleForMb(item) {
        this.targetSectionMB.forEach(article => {
            article.isShow = (article.id === item.id)
        })
        this.selectedArticle = item;
        this.isShowQr = this.showQrList.indexOf(item.label_names[0]) > -1;
        this.getSubscribeUrl();
        this.showArticleList = !this.showArticleList;
      },*/
      switchArticle(item) {
        this.targetSection.forEach(section => {
          section.articles.forEach(article => {
            article.isShow = (item.id === article.id)
          })
        })
        this.selectedArticle = item;
        this.isShowQr = this.showQrList.indexOf(item.label_names[0]) > -1
        this.getSubscribeUrl()
        if (!this.isPc) {
           // this.showArticleList = !this.showArticleList;
           this.showArticleList = false;
        }
      },
      openList(){
        this.showArticleList = !this.showArticleList;
        if (!this.showArticleList) {
          if (this.selectedArticle != null) {
            this.targetSection.forEach((item, index) => {
              let existSelectedItem = item.articles.filter(article =>{
                return article.isShow === true;
              });
              if (existSelectedItem && existSelectedItem.length > 0) {
                 if (!item.isShow) {
                    item.isShow = true;
                 }
              } else {
                item.isShow = false;
              }
            })
          }
        }
      },
      openClient(itemIndex) {
        if (!this.isPc && this.targetSection[itemIndex].isShow) {
           this.openList();
           return
        }
        this.targetSection.forEach((item, index) => {
          if (index === itemIndex) {
            item.isShow = true;
            if (this.isPc) {
              // only open first article for pc
              this.switchArticle(item.articles[0])
            }
          } else {
            item.isShow = false;
          }
        });
        // just for update the UI
        this.targetSection = this.targetSection.filter(item => {
          return true;
        });
      },

      onCopy() {
        // console.log('on copy')
      },
      onError() {
        // console.log('on error')
      },
      gotoRenew() {
        if (!!localStorage.getItem("email")) {
          this.isRenewLoading = true
          const that = this
          let param = {
            email: localStorage.getItem("email"),
            redirectTo: `${window.location.origin}/WHMCS`,
            goto: `viewinvoice.php?id=${this.lastInvoiceId}`
          }
          this.$request.getAutoLoginWHMCS(param, function (data) {
            that.isRenewLoading = false
            if (data.status === 201) {
              window.open(`${data.data}&language=chinese`, '_self')
            } else {
              window.open(`${window.location.origin}/WHMCS/viewinvoice.php?id=${this.lastInvoiceId}&language=chinese`, '_self')
            }
          })
        } else {
          window.open(`${window.location.origin}/WHMCS/viewinvoice.php?id=${this.lastInvoiceId}&language=chinese`, '_self')
        }
        // window.open(`${window.location.origin}/WHMCS/viewinvoice.php?id=${this.lastInvoiceId}`, '_self')
      },
      gotoUpgrade() {
        if (!!localStorage.getItem("email")) {
          this.isUpgradeLoading = true
          const that = this
          let param = {
            email: localStorage.getItem("email"),
            redirectTo: `${window.location.origin}/WHMCS`,
            goto: encodeURIComponent(`upgrade.php?type=configoptions&id=${this.serviceDetail.id}&language=chinese`)
          }
          this.$request.getAutoLoginWHMCS(param, function (data) {
            that.isUpgradeLoading = false
            if (data.status === 201) {
              window.open(`${data.data}`, '_self')
            } else {
              window.open(`${window.location.origin}/WHMCS/upgrade.php?type=configoptions&id=${this.serviceDetail.id}&language=chinese`, '_self')
            }
          })
        } else {
          window.open(`${window.location.origin}/WHMCS/upgrade.php?type=configoptions&id=${this.serviceDetail.id}&language=chinese`, '_self')
        }

        // window.open(`https://jetstream.surf/WHMCS/upgrade.php?type=configoptions&id=${this.serviceDetail.id}`, '_self')
      },
      openZw() {
        window.zE('webWidget', 'open');
        setTimeout(function () {
          // Open the channel
          document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
        }, 1000);
      },
    }
  };
</script>

