          .tutorial-list {
            position:absolute;
            width: 90%;
            z-index: 99;
            background-color: #FCFDFD;
            align-items: center;
            margin-left: 0.533rem;
            margin-right: 0.533rem;
            padding: 0.533rem 0.533rem;
            border: 0.027rem solid #E4EAF0;
            border-radius: 0.107rem;
            box-shadow: 0 0.08rem 0.16rem rgba(158, 183, 204, 0.2);
            .client-group {
              .client-item-container {
                width: 100%;
                display: flex;
                flex-direction: column;
                text-align: left;
                margin-top: 0.4rem;
                &:first-child {
                  margin-top: 0;
                }
                .item-header {
                  cursor: pointer;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: space-between;
                  margin-bottom: 0.4rem;

                  .client-title {
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    font-size: 0.427rem;
                    font-weight: 600;
                    line-height: 0.6rem;
                    /*height: 66px;*/
                    margin-bottom: 0;
                    //max-width: 113px;
                  }
                  .item-open-icon-close {
                    animation: mymove 0.1s;
                    animation-fill-mode: forwards;
                  }
                  .item-open-icon-open {
                    animation: mymove2 0.1s;
                    animation-fill-mode: forwards;
                  }
                  @keyframes mymove {
                    from {
                      transform: rotate(0deg);
                    }
                    to {
                      transform: rotate(45deg);
                    }
                  }
                  @keyframes mymove2 {
                    from {
                      transform: rotate(45deg);
                    }
                    to {
                      transform: rotate(0deg);
                    }
                  }
                  .item-close-icon {
                    background: center no-repeat;
                    background-size: contain;
                    width: 0.373rem;
                    height: 0.373rem;
                    display: inline-block;
                  }
                  &:hover {
                    .qa-title {
                      color: #3963EF;
                    }
                    .item-close-icon {
                      /*background: center no-repeat ;*/
                      background-size: contain;
                    }
                  }
                  .client-item-enter-active {
                    transition: transform .3s ease;
                  }
                  .client-item-leave-active {
                    transition: transform .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
                  }
                  .client-item-enter, .client-item-leave-to
                    /* .slide-fade-leave-active for below version 2.1.8 */
                  {
                    transform: translateX(10px);
                    opacity: 0;
                  }
                  &:hover {
                    cursor: pointer;
                  }
                }
                .item-header-opacity {
                  opacity: 0.4;
                }
                .content-list {
                  max-height: fit-content;
                  height: 0;
                  overflow: hidden;
                  transition: height ease-out 0.2s;
                  .client-name-content {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    margin-bottom: 0.533rem;
                    .client-name {
                      font-size: 0.427rem;
                      font-weight: 400;
                      line-height: 0.8rem;
                      color: #3963EF;
                      cursor: pointer;
                      display: flex;
                      align-items: center;
                      margin-bottom: 0;
                    }
                  }
                  .client-name-content-opacity {
                    opacity: 0.4;
                    .client-name {
                      color: #616577;

                    }
                  }
                }
                .content-list-open {
                  height: 10.067rem;
                  transition: height ease-in 0.2s;
                }
              }
            }
          }
